@import 'base.scss';

header {
    margin: 0;
    padding: 0;
    height: 38vh;
    top: 0;
    left: 0;

    nav {
        display: flex;
        justify-content: space-evenly;
        background: url("../media/topo-bg.png");
        background-size: 10%;
        background-position-y: bottom;
        height: 8vh;
        opacity: 0.8;

        .nav-btn {
            width: 30%;
            font-size: 3vw;
            opacity: 1;
            padding: 0;
            margin: 10px 0;
        }
    }

    .banner {
        background-image: url("https://images.unsplash.com/photo-1508672019048-805c876b67e2?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1586&q=80");
        background-size: cover;
        opacity: 0.9;
        margin: 0;
        padding: 0;
        width: 100vw;
        height: 30vh;
        z-index: -1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
}

/* DESKTOP */
@media only screen and (min-width: 1024px) {

    header {
        height: 65vh;

        nav {
            justify-content: center;

            .nav-btn {
                width: 10%;
                font-size: 1vw;
                opacity: 1;
                padding: 0;
                margin: 10px 20px;
            }
        }

        .banner,
        .banner-text {
            height: 55vh;
        }
    }
}